.row {
  margin-left: 20px;
  color: #fff;
}
.row_title {
  color: #fff;
}
.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 200px;
  margin-right: 20px;
  transition: transform 450ms;
}

.row__poster:hover {
  transform: scale(1.08);
}

.row__posterlarge {
  max-height: 350px;
}

.row__posterlarge:hover {
  transform: scale(1.09);
}
